import { SpecificChartConfig } from '../../types';
import { chartInReverse } from '../../utils';
import { merge } from 'lodash-es';

const defaultHide = {
  axisLine: {
    show:false // 
  },
  splitLine: {
    show: false // 
  },
  axisTick: { // 
    show: false
  },
  // axisPointer: { //  hover line/shadow
  //   show: false
  // },
}

const horizontal = {
  xAxis: {
    show: false, //  x 
    ...defaultHide
  },
  yAxis: {
    show: true, //  y 
    ...defaultHide
  },
  grid: {
    top: 15
  }
}

const vertical = {
  xAxis: {
    show: true, //  x 
    ...defaultHide
  },
  yAxis: {
    show: false, //  y 
    ...defaultHide
  },
}

const hideAxis = (xAxis: {[index:string]: any}, yAxis: {[index:string]: any}) => {
  return chartInReverse(xAxis, yAxis) ? horizontal : vertical;
}

// hideAxis  x y 
const config: SpecificChartConfig = {
  defaultChartProps: {},
  optionHandler: (options) => {
    const {xAxis, yAxis} = options;
    merge(options, hideAxis(xAxis, yAxis));
  }
}

export default config;
